@import "../../Variables.scss";
    
.project {
    @include responsiveHeights70;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    background-color: $Grayish;

    @media screen and (max-width: 767px){
        display: flex;
        flex-direction: column;
        height: 60vh;
    }

    .projectDetails {
        grid-row: 1/3;

        display: flex;
        flex-direction: column;
        justify-self: center;
        align-self: center;

        padding: 20px;
            
        h2 {
            @include montserratRegular;
            @include TitleFontSizes;
            letter-spacing: 8px;
            color: $Blackish;
            margin: 0;
            align-self: center;
            text-align: center;
        }
        p{
            @include charterLight;
            @include SubTitleFontSizes;
            
            color: $Blackish;
            letter-spacing: 1px;
            margin: 0;
            padding: 15px;
            margin-top: 5px;
        }
        .websiteLinks{
            display: flex;
            align-self: center;
            justify-self: center;
            justify-content: center;
            justify-items: center;
            a {
                @include montserratRegular;
                @include NormalFontSizes;
                
                color: $Blackish;
                margin: 0;
                margin-top: 5px;
                text-decoration: none;

                &:hover{
                    text-decoration: underline;
                }
            }
            p{
                padding: 0;
                padding-left: 5px;
                padding-right: 5px;
            }
        }
    }

    .projectPics {
        align-self: center;
        
        img {
            max-width: 35vw;
            height: auto;
            max-height: 30vh;
            object-fit: scale-down;
            @media screen and (max-width: 767px){
                max-height: 10vh;
            }
        }
    }

    .left{
        grid-column: 1/2;
    }
    .right{
        grid-column: 2/3;
    }

    .project-BG{
        grid-column: 1/3;
        grid-row: 2/3;
        background-color: $Whiteish;
    }
}
.altColor{
    background-color: $Mauve;
    margin-top: 60px;
    margin-bottom: 30px;
}
