
@import "../../Variables.scss";

.jobContainer {
    @include responsiveHeights70;
    
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 0.5fr 0.5fr 1fr 4fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    justify-content: center;
    align-content: center;
    background-color: $Pale;

    @media screen and (max-width: 767px){
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        height: 50vh;
    }

    .jobDetails {
        align-self: center;
        justify-self: center;
        text-align: center;
        grid-row: 2/6;
        grid-column: 2/8;
        background-color: $Pink;
        padding: 20px;

        @media screen and (min-width: 768px) and (max-width: 1023px){
            grid-column: 2/9;
            justify-self: flex-end;
        }
        @media screen and (max-width: 767px){
            background-color: $Pale;
        }

        h2 {
            @include montserratRegular;
            @include TitleFontSizes;
            
            color: $Blackish;
            letter-spacing: 2px;
            grid-row: 1/2;
            grid-column: 1/3;
        }
        h3 {
            @include charterLight;
            @include SubTitleFontSizes;
            
            color: $Blackish;
            display: inline;
            letter-spacing: 1px;
        }
        p {
            @include charterLight;
            @include NormalFontSizes;

            color: $Blackish;
            text-align: left;
            padding: 10px;
        }
    }

    .imageLogo {
        justify-self: center;
        align-self: center;
        grid-column: 10/11;
        grid-row: 3/5;
        img {
            max-width: 30vw;
            height: auto;
        }
    }
}


